<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'last',
  data() {
    return {
      last_1: '',
      last_2: '',
      last_3: '',
    };
  },
  computed: {
    ...mapGetters(['getSurveyFinish']),
    ...mapGetters(['getUserContentId']),
  },
  methods: {
    ...mapActions(['surveyRanking']),
    ...mapActions(['setNotice']),
    ...mapActions(['setSurveyFinishF']),
    lastSubmit() {
      if (
        this.last_1 === ''
        || this.last_2 === ''
        || this.last_3 === ''
      ) {
        this.setNotice({
          noticeTitle: ' ',
          noticeBody: '    ',
          noticeButton: '',
          noticeHeight: '100%',
          noticeDisplay: 'inline-block',
        });
        return;
      }
      this.surveyRanking({
        rank1: this.last_1,
        rank2: this.last_2,
        rank3: this.last_3,
      });
      this.setSurveyFinishF({ surveyFinish: true });
      this.setNotice({
        noticeTitle: '  ',
        noticeBody: '  ',
        noticeButton: '',
        noticeHeight: '100%',
        noticeDisplay: 'inline-block',
      });
    },
  },
};
</script>

